 模板
<template>
  <!-- 主体区域 -->
  <section id="app">
    <!-- 头部组件，用于添加待办事项 -->
    <toda-header @add="changeName"></toda-header>
    <!-- 主要显示区域，用于展示待办事项列表 -->
    <toda-main :list="list" @del="delToda"></toda-main>
    <!-- 底部组件，用于统计和清除待办事项 -->
    <toda-footer :list="list" @delTotal="delTotal"></toda-footer>
  </section>
</template>

<script>
// 导入所需的子组件
import todaHeader from './components/todaHeader.vue'
import todaMain from './components/todaMain.vue'
import todaFooter from './components/todaFooter.vue'

// 渲染功能
// 1. 提供数据 => 提供在公共父组件 App.vue
// 2. 通过父传子，将数据传递给todaMain
// 3. 利用v-for进行渲染

// 添加功能
// 1. 收集表单数据 v-model
// 2. 监听事件（回车 + 点击 都需要进行监听）
// 3. 子传父，在父组件进行数据的添加功能

// 删除功能
// 1. 监听点击事件，给x号
// 2. 子传父，将删除的id传递给父组件，进行删除
// 3. 进行删除 使用filter

// 底部合计和清除功能
// 1. 父传子list，在子中统计数据，在底部进行展示
// 2. 父传子，监听点击事件，进行删除

// 持久化存储
// 使用watch进行深度监视
export default {
  // 定义组件初始数据
  data() {
    return {
      // 待办事项列表，包含多个待办事项对象，每个对象有id和name属性
      list:JSON.parse(localStorage.getItem('list')) || [
        {id: 1, name: '唱'},
        {id: 2, name: '跳'},
        {id: 3, name: 'rap'},
        {id: 4, name: '篮球'},
      ],
    }
  },
  // 注册子组件
  components: {
    todaFooter,
    todaMain,
    todaHeader
  },
  // 定义组件方法
  methods: {
    /**
     * 添加待办事项
     * @param {String} newName - 新待办事项名称
     * @desc
     * 通过unshift方法，将新待办事项添加到list数组的开头，并更新list数据
     */
    changeName(newName) {
      const id = +new Date();
      this.list.unshift({id, name: newName})
    },
    /**
     * 删除待办事项
     * @param {Number} id - 要删除的待办事项的ID
     * @desc
     * 通过filter方法，过滤掉要删除的待办事项，并更新list数据
     */
    delToda(id) {
      this.list = this.list.filter(item => item.id !== id);
    },
    /**
     * 删除所有待办事项
     * @desc
     * 将list数组置为空数组，清空所有待办事项
     */
    delTotal(){
      this.list = [];
    }
  },
  watch:{
    list:{
      deep:true,
      handler(newValue){
        localStorage.setItem('list',JSON.stringify(newValue))
      }
    }
  }
}
</script>

