 模板
<script>
export default {
  // 定义初始数据
  data(){
    return {
      // 待添加任务的名称
      todaName:''
    }
  },
  // 定义方法
  methods:{
    /**
     * 添加任务
     * @desc
     * 通过$emit方法向父组件发送'add'事件，传递todaName作为参数
     */
    changeName(){
      if (!this.todaName){
        alert('请输入任务名')
        return;
      }
      this.$emit('add',this.todaName)
      this.todaName=''
    }
  }
}
</script>

<template>
  <!-- 页面头部区域 -->
  <header class="header">
    <!-- 标题 -->
    <h1>小黑记事本</h1>
    <!-- 任务名称输入框 -->
    <input placeholder="请输入任务" class="new-todo" v-model="todaName" @keyup.enter="changeName"/>
    <!-- 添加任务按钮 -->
    <button class="add" @click="changeName">添加任务</button>
  </header>
</template>

<style scoped>

</style>
