 模板
<script>
export default {
  // 定义组件的属性
  props: {
    list: Array // 从父组件接收的待办事项列表
  },
  // 定义组件的方法
  methods: {
    /**
     * 删除所有待办事项
     * @desc
     * 通过$emit方法向父组件发送'delTotal'事件
     */
    delTotalToda(){
      this.$emit('delTotal')
    }
  },
  // 定义组件的计算属性
  computed: {
    /**
     * 计算属性：计算待办事项的总数
     * @return {Number} list的长度
     */
    totalCount() {
      return this.list.length
    }
  }
}
</script>

<template>
  <!-- 统计和清空 -->
  <footer class="footer">
    <!-- 统计 -->
    <span class="todo-count">合 计:<strong> {{ totalCount }} </strong></span>
    <!-- 清空 -->
    <button class="clear-completed" @click="delTotalToda">
      清空任务
    </button>
  </footer>
</template>

