 模板
<script>
// 导出一个默认的Vue组件
export default {
  // 定义组件的属性，list属性是一个数组类型
  props: {
    list: Array
  },
  // 定义组件的方法
  methods: {
    /**
     * 发送删除事件
     * @param {Number} id - 要删除的todo项目的ID
     * @desc
     * 通过$emit方法向父组件发送'del'事件，传递ID作为参数
     */
    delToda(id) {
      this.$emit('del', id);
    },
  }
}
</script>

<template>
  <!-- 列表区域 -->
  <section class="main">
    <!-- 遍历list数组，为每个todo项目生成一个li元素 -->
    <ul class="todo-list">
      <li class="todo" v-for="(item,index) in list" :key="item.id">
        <div class="view">
          <!-- 显示todo项目的序号 -->
          <span class="index">{{ index + 1 }}.</span>
          <!-- 显示todo项目的名称 -->
          <label>{{ item.name }}</label>
          <!-- 点击按钮时触发delToda方法，传入todo项目的ID -->
          <button @click="delToda(item.id)" class="destroy"></button>
        </div>
      </li>
    </ul>
  </section>
</template>

<style scoped>

</style>
